<style>:global(h1,h2,h3,h4,h5,h6){margin-top:1rem;margin-bottom:.5rem}:global(p){margin-bottom:.5rem;text-indent:1.1rem;line-height:1.3rem}:global(li){margin-left:2rem;margin-bottom:.5rem}:global(td),:global(th){padding-left:2rem}:global(th){font-weight:bolder}:global(#aspects #trouble){-moz-column-break-before:column;break-before:column}.single-column-content{text-align:justify}.two-column-content{-moz-column-count:2;column-count:2;text-align:justify}.three-column-content{-moz-column-count:3;column-count:3;text-align:justify}.four-column-content{-moz-column-count:4;column-count:4;text-align:justify}section{display:block}section:after{clear:both}.page{width:8.5in;height:auto;padding:.5in;margin:5rem auto;overflow:hidden;box-shadow:0 4px 8px 0 rgba(0,0,0,.2),0 6px 20px 0 rgba(0,0,0,.19)}</style>

<script>
    import marked from 'marked';
    export let markdownFile;
    export let markdown;
    if (!markdown) {
        fetch(markdownFile).then((response) => response.text().then((data) => markdown = data));
    }
    export let props={};
    export let columns=2;
    let columnClass="two-column-content";
    let lookup={
        1: "single-column-content",
        2: "two-column-content",
        3: "three-column-content",
        4: "four-column-content",
        "not-found":"two-column-content"
    };

    if (columns in lookup) {
        columnClass=lookup[columns];
    }
    window.scrollTo(0,0);
</script>

{#if markdown}

<section class="{columnClass} page" {...props}>
    {@html marked(markdown)}
</section>
{/if}
